import axios from "axios";
import { createStore } from "vuex";

const store = createStore({
  state () {
    return {
      name : 'kim' ,
      age : 20 ,
      likes : 30,
      likesyn : true,
      more : {},
    }
  },
  mutations : {
    setMore(state, data) {
      state.more = data
    },    
    이름변경(state){
      state.name = 'park'
    },
    나이변경(state){
      state.age++;
    },
    좋아요변경(state){
      if(state.likesyn) {
        state.likes++ ;
        state.likesyn = false;
      }else {
        state.likes-- ;
        state.likesyn = true;
      }
    }    
  },
  actions : {
    getData() {
      axios.get('https://codingapple1.github.io/vue/more0.json')
      .then((a) =>{
        console.log(a.data);
        this.commit('setMore', a.data);
      })
    }
  }
})

export default store;